import { Component, OnInit, NgZone } from '@angular/core';

import * as moment from 'moment';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { CalendarService } from '../../services/calendar/calendar.service';
import { Event } from '../../services/calendar/event';
import { ActivatedRoute } from '@angular/router';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { Employee } from 'src/app/staff/services/employees/employee';

@Component({
  selector: 'app-calendar-page',
  templateUrl: './calendar-page.component.html',
  styleUrls: ['./calendar-page.component.scss']
})
export class CalendarPageComponent implements OnInit {

  public months: string[] = ['январь', 'февраль', 'март', 'апрель', 'май', 'июнь',
    'июль', 'август', 'сентябрь', 'октябрь', 'ноябрь', 'декабрь'];
  public monthsIn: string[] = ['января', 'февраля', 'марта', 'апреля', 'мая', 'июня',
    'июля', 'августа', 'сентября', 'октября', 'ноября', 'декабря'];

  public day: number = null;
  public month: number = null;
  public year: number = null;

  public activeDay: number = null;
  public activeDayOfWeek: number = null;
  public activeMonth: number = null;
  public activeYear: number = null;
  public activeDate: Date = null;

  public days = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'];
  public daysFull = ['Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'];

  public numbers = [];

  legends = [{
      mark: '#da402b',
      name: 'События компании'
    },
    {
      mark: '#27EE2F',
      name: 'События комбината'
    },
    {
      mark: '#B016CC',
      name: 'События в отрасли'
    },
    {
      mark: '#EEA127',
      name: 'События подразделения'
    }];

  events: Event[] = [];
  activeEvents: Event[] = [];

  private employee: Employee = null;

  constructor(private sidebarService: SidebarService,
    private calendarService: CalendarService,
    private route: ActivatedRoute,
    private sharepointService: SharepointService) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  ngOnInit() {
    this.employee = this.route.snapshot.data.employee;
    this.resetDate();
  }

  setMonth(month: number, year: number) {
    this.month = month;
    this.year = year;
    const date = new Date(year, month, 1);
    this.numbers = [];
    if (moment(date).isoWeekday() < 8) {
      for (let i = 1; i < moment(date).isoWeekday(); i++) {
        this.numbers.push(0);
      }
    }
    for (let i = 1; i <= moment(date).daysInMonth(); i++) {
      this.numbers.push(i);
    }
    this.load();
  }

  resetDate() {
    this.activeDate = new Date();
    this.activeDay = this.activeDate.getDate();
    this.activeDayOfWeek = moment(this.activeDate).isoWeekday();
    this.month = this.activeDate.getMonth();
    this.activeMonth = this.activeDate.getMonth();
    this.year = this.activeDate.getFullYear();
    this.activeYear = this.activeDate.getFullYear();
    this.setMonth(this.month, this.year);
    this.loadActive();
  }

  setDate(day: number, month: number, year: number) {
    if (day > 0) {
      this.activeDay = day;
      this.activeMonth = month;
      this.activeYear = year;
      this.activeDate = new Date(this.activeYear, this.activeMonth, this.activeDay);
      const date = moment(this.activeDate);
      this.activeDayOfWeek = date.isoWeekday();
      this.loadActive();
    }
  }

  load() {
    this.calendarService.getItemsBy(this.month, this.year, this.employee.lookupDepartmentId, this.employee.company)
      .subscribe((events: Event[]) => {
          this.events = events;
      });
  }

  loadActive() {
    this.calendarService.getItemsBy(this.month, this.year, this.employee.lookupDepartmentId, this.employee.company)
      .subscribe((events: Event[]) => {
          this.events = events;
          this.activeEvents = events.filter(
            e => (this.activeDate.getTime() >= e.startWithoutTime.getTime() && (e.endWithoutTime == null
              || this.activeDate.getTime() <= e.endWithoutTime.getTime())));
      });
  }

  hasEvent(events: Event[], day: number, month: number, year: number): boolean {
    const date = new Date(year, month, day).getTime();
    return events.some(e => (date >= e.startWithoutTime.getTime() && date <= e.endWithoutTime.getTime()));
  }

  edit(event, item: Event) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slCalendar/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(event, item: Event) {
    this.calendarService.recycleItem(item)
      .subscribe(() => this.load());
  }
}
