import { Component, OnInit } from '@angular/core';

import * as moment from 'moment';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { Event } from '../../services/calendar/event';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-event-card-page',
  templateUrl: './event-card-page.component.html',
  styleUrls: ['./event-card-page.component.scss']
})
export class EventCardPageComponent implements OnInit {

  public event: Event;
  public day: number = null;
  public month: number = null;
  public dayOfWeek: number = null;

  public monthsIn: string[] = ['января', 'февраля', 'марта', 'апреля', 'мая', 'июня',
    'июля', 'августа', 'сентября', 'октября', 'ноября', 'декабря'];
  public daysFull = ['Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'];

  constructor(
    private sidebarService: SidebarService,
    private route: ActivatedRoute
  ) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  ngOnInit() {
    this.event = this.route.snapshot.data.event;
    this.day = this.event.start.getDate();
    this.month = this.event.start.getMonth();
    this.dayOfWeek = moment(this.event.start).isoWeekday();
  }

}
