import { Pipe, PipeTransform } from '@angular/core';
import { Event } from './event';

@Pipe({
    name: 'CurrentEvents',
    pure: false
})
export class CurrentEventsPipe implements PipeTransform {
    transform(items: Event[], filter: Date): any {
      const date = filter.getTime();
      return filter
        ? items.filter(e => (date >= e.startWithoutTime.getTime() && (e.endWithoutTime == null || date <= e.endWithoutTime.getTime())))
        : [];
    }
}
