import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ElementRef
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { groups } from 'src/app/constants/groups';
import { SecurityService } from 'src/app/services/security/security.service';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';

@Component({
  selector: 'app-create-event-button',
  templateUrl: './create-event-button.component.html',
  styleUrls: ['./create-event-button.component.scss']
})
export class CreateEventButtonComponent implements OnInit {
  public isActivated: Boolean = false;
  public showAlret: Boolean = false;
  public isShow = false;

  constructor(
    private route: ActivatedRoute,
    private componentRef: ElementRef,
    private securityService: SecurityService,
    private sharepointService: SharepointService) { }

  ngOnInit() {
    if (this.route.snapshot.data.groups) {
      const g = this.route.snapshot.data.groups;
      if (g && g.includes(groups.administrator)) {
        this.isShow = true;
      }
    } else {
      this.securityService.getCurrentUserGroups()
        .subscribe((g: string[]) => {
          if (g && g.includes(groups.administrator)) {
            this.isShow = true;
          }
        });
    }
  }

  public mouseLeave() {
    this.isActivated = false;
  }

  public onClick(event) {
    if (this.isActivated &&
      !this.componentRef.nativeElement.contains(event.target)) {
      this.closeControls(event);
    }
  }

  public handleCreateCelebration(event) {
    event.stopPropagation();
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slCalendar/NewForm.aspx?ContentTypeId=0x010200C06DFD6907A0420D89ADF28F00117C8D&Source=`
      + encodeURIComponent(window.location.href);
    this.closeControls(event);
  }

  public handleCreateCompany(event) {
    event.stopPropagation();
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slCalendar/NewForm.aspx?ContentTypeId=0x0102006BFE458C64184AEB935F128D1B59E157&Source=`
      + encodeURIComponent(window.location.href);
    this.closeControls(event);
  }

  public handleCreateCombine(event) {
    event.stopPropagation();
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slCalendar/NewForm.aspx?ContentTypeId=0x010200898FF7141CD241DABAF49C72742B05B5&Source=`
      + encodeURIComponent(window.location.href);
    this.closeControls(event);
  }

  public handleCreateDepartment(event) {
    event.stopPropagation();
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slCalendar/NewForm.aspx?ContentTypeId=0x010200CBF1DB022D8044CBA0EA930C77282B1D&Source=`
      + encodeURIComponent(window.location.href);
    this.closeControls(event);
  }

  public handleCreateIndustry(event) {
    event.stopPropagation();
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slCalendar/NewForm.aspx?ContentTypeId=0x010200F4E6B239C55D41CC8690E4005878D7BA&Source=`
      + encodeURIComponent(window.location.href);
    this.closeControls(event);
  }

  public closeControls(event) {
    event.stopPropagation();
    this.showAlret = false;
    this.isActivated = false;
  }

  public openControls(event) {
    event.stopPropagation();
    this.showAlret = false;
    this.isActivated = true;
  }
}
