import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CalendarPageComponent } from './components/calendar-page/calendar-page.component';
import { EventCardPageComponent } from './components/event-card-page/event-card-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';
import { EventResolver } from './services/calendar/event.resolver';
import { CurrentEmployeeResolver } from './services/employees/current-employee.resolver';
// import { MsalGuard } from '@azure/msal-angular';

const routes: Routes = [
  {
    path: '',
    component: CalendarPageComponent,
    // canActivate : [MsalGuard],
    resolve: { groups: GroupsResolver, employee: CurrentEmployeeResolver },
    data: { animation: 'fadeAnimation', title: 'Календарь событий' }
  },
  {
    path: ':id',
    component: EventCardPageComponent,
    resolve: { groups: GroupsResolver, event: EventResolver },
    data: { animation: 'fadeAnimation', title: 'Календарь событий' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [ GroupsResolver, EventResolver, CurrentEmployeeResolver ],
    exports: [RouterModule]
})
export class CalendarRoutingModule { }
