import { Injectable } from '@angular/core';

import * as moment from 'moment';

import { Event } from './event';
import { ListItemsService, IListItemsServiceParams } from 'src/app/services/list-items-service';
import { Observable, from, of } from 'rxjs';
import { flatMap, map } from 'rxjs/operators';
import { RecurrenceService } from 'src/app/shared/services/recurrence/recurrence.service';
import { Web, Items } from '@pnp/sp';


@Injectable({
  providedIn: 'root'
})
export class CalendarService extends ListItemsService<Event> {

  constructor(private recurrenceService: RecurrenceService) {
    super();
  }

  getListTitle(): string {
    return 'Календарь событий';
  }

  getListRelativePath(): string {
    return 'lists/slCalendar';
  }

  getSelect(): string {
    return 'Id,Title,StartDate,EndDate,slEventDescription,slEventPlaceLookup/Title,'
      + 'fAllDayEvent,RecurrenceData,Duration,fRecurrence,ContentType/Name,'
      + 'slCompanyLookup/Title,slDepartmentLookup/Title,'
      + 'slCompanyLookup/slCode,slCompanyLookup/Id,slDepartmentLookup/Id';
  }

  getExpand(): string {
    return 'slEventPlaceLookup,slCompanyLookup,slDepartmentLookup,ContentType';
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  getCachingOptions(): import('@pnp/odata').ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): Event {
    const entity: Event = {
      id: item.Id,
      title: item.Title,
      description: item.slEventDescription,
      type: item.ContentType && this.getTypeName(item.ContentType.Name)
    };
    if (!(item.StartDate instanceof Date)) {
      entity.start = moment(item.StartDate.slice(0, -1), 'YYYY-MM-DD HH:mm:ss').toDate();
    } else {
      entity.start = item.StartDate;
    }
    if (!(item.EndDate instanceof Date)) {
      entity.end = moment(item.EndDate.slice(0, -1), 'YYYY-MM-DD HH:mm:ss').toDate();
    } else {
      entity.end = item.EndDate;
    }
    if (entity.start) {
      entity.startWithoutTime = new Date(entity.start.getFullYear(), entity.start.getMonth(), entity.start.getDate());
      entity.startTitle = moment(entity.start).format('HH:mm');
    }
    if (entity.end) {
      entity.endWithoutTime = new Date(entity.end.getFullYear(), entity.end.getMonth(), entity.end.getDate(), 23, 59, 59);
      entity.endTitle = moment(entity.end).format('HH:mm');
    }
    if (item.slEventPlaceLookup) {
      entity.placeId = item.slEventPlaceLookup.Id;
      entity.placeTitle = item.slEventPlaceLookup.Title;
    }
    if (item.slCompanyLookup) {
      entity.companyId = item.slCompanyLookup.map(c => c.Id);
      entity.companyTitle = item.slCompanyLookup.map(c => c.Title);
    }
    if (item.slDepartmentLookup) {
      entity.departmentId = item.slDepartmentLookup.map(d => d.Id);
      entity.departmentTitle = item.slDepartmentLookup.map(d => d.Title);
    }
    return entity;
  }

  public getItemsByEventPeriod(params: IListItemsServiceParams, start: Date, end: Date): Observable<Event[]> {
    return this.getWebById(params && params.webId).pipe(
      flatMap((web: Web) => this.getItemsOfWebByEventPeriod(web, params, start, end)));
  }

  protected getItemsOfWebByEventPeriod(web: Web, params: IListItemsServiceParams,
    start: Date, end: Date): Observable<Event[]> {
    let data: Observable<Event[]>;
    if (this.isMock() === false) {
      const items: Items
        = web.lists.getByTitle(this.getListTitle()).items;
      this.setQueryForCollection(items, params);
      const cache = this.getCachingOptions();
      if (cache != null) {
        cache.key += this.hashCode(`*getItemsPagedOfWeb*${items.toUrlAndQuery()}`);
        items.usingCaching(cache);
      }
      data = from(items.get().then((rows: any[]) => {
        rows = this.recurrenceService.parseEvents(rows, start, end);
        return rows.map(i => this.convertListItemToEntity(i));
      }));
    } else {
      data = of(this.getMockItems());
    }
    return data;
  }

  convertEntityToListItem(entity: Event) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Event[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  private getTypeName(type: string) {
    switch(type) {
      case 'Контентный тип ­события комбината': return '­Событие комбината'; break;
      case 'Контентный тип ­события подразделения': return 'Событие подразделения'; break;
      case 'Контентный тип ­праздник': return '­Праздник'; break;
      case 'Контентный тип ­события компании': return 'Событие компании'; break;
      case 'Контентный тип ­события в отрасли': return '­Событие в отрасли'; break;
      default: break;
    }

  }

  public getItemsBy(month: number, year: number, departmentId: number, companyCode: string, webId?: string): Observable<Event[]> {
    const start = (new Date(year, month, 1)).toISOString();
    const end = (new Date(year, month + 1, 1)).toISOString();
    const filter = `((StartDate ge datetime'${start}' and StartDate lt datetime'${end}') `
    + ` or (EndDate ge datetime'${start}' and EndDate lt datetime'${end}')`
    + ` or (StartDate lt datetime'${start}' and EndDate ge datetime'${end}'))`
    + ` and ((ContentType eq 'Контентный тип ­события комбината' and slCompanyLookup/slCode eq '${companyCode}')`
      + ` or ContentType ne 'Контентный тип ­события комбината')`
    + ` and ((ContentType eq 'Контентный тип ­события подразделения' and slDepartmentLookup/Id eq ${departmentId || 0})`
      + ` or ContentType ne 'Контентный тип ­события подразделения')`;
    return this.getItemsByEventPeriod({filter: encodeURI(filter), webId: webId}, new Date(year, month, 1), new Date(year, month + 1, 1));
  }
}
